// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from 'vue'
import App from './App'
import router from './router'
// import Notifications from 'vue-notification'
import toastr from 'vue-toastr'
import Vuetify from 'vuetify'
import VueTyper from 'vue-typer'
import VueContentPlaceholders from 'vue-content-placeholders'
// import Vuetify from '@/plugins/vuetify'

import 'vuetify/dist/vuetify.min.css'
// require('../node_modules/bootstrap/dist/css/bootstrap.css')
require('./assets/approck/bootstrap/css/bootstrap.min.css')
require('./assets/approck/css/animate.css')
require('./assets/approck/css/jquery.carousel-3d.default.css')
require('./assets/approck/css/style.css')

require('./assets/approck/js/jquery-1.12.4.min.js')
require('./assets/approck/bootstrap/js/bootstrap.min.js')
// require('./assets/approck/js/modernizr-2.8.3.min.js')
// require('./assets/approck/js/jquery.stellar.min.js')
// require('./assets/approck/js/jquery.resize.ex.js')
// require('./assets/approck/js/waitforimages.js')
// require('./assets/approck/js/jquery.carousel-3d.min.js')

require('./assets/approck/js/wow.min.js')
require('./assets/approck/js/scripts.js')

// Vue.use(Notifications)
Vue.use(toastr)
Vue.use(Vuetify)
Vue.use(VueTyper)
Vue.use(VueContentPlaceholders)

Vue.config.productionTip = false

Vue.mixin({
  data: function () {
    return {
      loading: true
    }
  }
})
// const opts = {}

// export default new Vuetify(opts)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  // vuetify: new Vuetify(opts),
  router,
  // opts,
  components: { App },
  template: '<App/>'
})



